//  Supabase 
export const supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY)

// 
export class SyncService {
  static async saveData(userId, key, data) {
    try {
      const storageKey = `study-tracker-${userId}-${key}`
      
      console.log('  Supabase:', { userId, key, storageKey })
      
      // 1.  localStorage
      localStorage.setItem(storageKey, JSON.stringify(data))
      
      // 2.  Supabase -  upsert  insert
      const { error } = await supabase
        .from('user_data')
        .upsert({
          user_id: userId,
          data_key: storageKey,
          data_value: data,
          updated_at: new Date().toISOString()
        }, {
          onConflict: 'user_id, data_key'  // 
        })
      
      if (error) {
        console.warn(' Supabase :', error.message)
        return false
      } else {
        console.log(' Supabase :', key)
        return true
      }
      
    } catch (error) {
      console.error(' :', error)
      return false
    }
  }

  static async loadData(userId, key) {
    try {
      const storageKey = `study-tracker-${userId}-${key}`
      
      console.log('  Supabase :', { userId, key, storageKey })
      
      // 1.  localStorage 
      const localData = localStorage.getItem(storageKey)
      if (localData) {
        console.log(' :', key)
        return JSON.parse(localData)
      }
      
      // 2.  Supabase 
      const { data: supabaseData, error } = await supabase
        .from('user_data')
        .select('data_value')
        .eq('user_id', userId)
        .eq('data_key', storageKey)
        .single()
      
      if (error) {
        console.log(' Supabase :', key, error.message)
        return null
      }
      
      if (!supabaseData) {
        console.log(' Supabase :', key)
        return null
      }
      
      //  localStorage 
      localStorage.setItem(storageKey, JSON.stringify(supabaseData.data_value))
      console.log('  Supabase :', key)
      return supabaseData.data_value
      
    } catch (error) {
      console.error(' :', error)
      return null
    }
  }
}