const ACHIEVEMENTS_CONFIG = {
  // 
  beginner: [
    {
      id: 'first_task',
      name: '',
      description: '',
      icon: '',
      condition: (userData) => {
        const allTasks = Object.values(userData.tasksByDate).flat();
        return allTasks.some(task => task.done);
      },
      points: 5
    },
    {
      id: 'first_30min',
      name: '', 
      description: '30',
      icon: '',
      condition: (userData) => {
        const today = new Date().toISOString().split('T')[0];
        const todayTime = userData.tasksByDate[today]?.reduce((sum, task) => sum + (task.timeSpent || 0), 0) || 0;
        return todayTime >= 1800;
      },
      points: 10
    },
    {
      id: 'plan_master',
      name: '',
      description: '10',
      icon: '',
      condition: (userData) => {
        const allTasks = Object.values(userData.tasksByDate).flat();
        return allTasks.length >= 10;
      },
      points: 15
    }
  ],
  
  // 
  time: [
    {
      id: 'one_hour',
      name: '',
      description: '1',
      icon: '',
      condition: (userData) => {
        const today = new Date().toISOString().split('T')[0];
        const todayTime = userData.tasksByDate[today]?.reduce((sum, task) => sum + (task.timeSpent || 0), 0) || 0;
        return todayTime >= 3600;
      },
      points: 20
    },
    {
      id: 'three_hours',
      name: '',
      description: '3', 
      icon: '',
      condition: (userData) => {
        const today = new Date().toISOString().split('T')[0];
        const todayTime = userData.tasksByDate[today]?.reduce((sum, task) => sum + (task.timeSpent || 0), 0) || 0;
        return todayTime >= 10800;
      },
      points: 40
    }
  ],
  
  // 
  streak: [
    {
      id: 'three_days',
      name: '',
      description: '3',
      icon: '',
      condition: (userData) => {
        return calculateCurrentStreak(userData.tasksByDate) >= 3; // 
      },
      points: 25
    },
    {
      id: 'one_week',
      name: '',
      description: '7',
      icon: '',
      condition: (userData) => {
        return calculateCurrentStreak(userData.tasksByDate) >= 7; // 
      },
      points: 50
    },
    {
      id: 'one_month',
      name: '',
      description: '30',
      icon: '',
      condition: (userData) => {
        return calculateCurrentStreak(userData.tasksByDate) >= 30; // 
      },
      points: 100
    }
  ],
  
  // 
  subject: [
    {
      id: 'math_lover',
      name: '',
      description: '2',
      icon: '',
      condition: (userData) => {
        const allTasks = Object.values(userData.tasksByDate).flat();
        const mathTime = allTasks
          .filter(task => task.category === '')
          .reduce((sum, task) => sum + (task.timeSpent || 0), 0);
        return mathTime >= 7200;
      },
      points: 30
    },
    {
      id: 'english_master',
      name: '',
      description: '2',
      icon: '',
      condition: (userData) => {
        const allTasks = Object.values(userData.tasksByDate).flat();
        const englishTime = allTasks
          .filter(task => task.category === '')
          .reduce((sum, task) => sum + (task.timeSpent || 0), 0);
        return englishTime >= 7200;
      },
      points: 30
    },
    {
      id: 'balanced',
      name: '',
      description: '',
      icon: '',
      condition: (userData) => {
        const allTasks = Object.values(userData.tasksByDate).flat();
        const studiedCategories = new Set(allTasks.map(task => task.category));
        return categories.every(cat => studiedCategories.has(cat.name));
      },
      points: 40
    }
  ],
  
  custom: [
    // 
  ],

  // 
  special: [
   {
    id: 'early_bird',
    name: '',
    description: '6-8',
    icon: '',
    condition: (userData) => {
      const allTasks = Object.values(userData.tasksByDate).flat();
      return allTasks.some(task => {
        if (task.done && task.timeSegments) {
          return task.timeSegments.some(segment => {
            if (segment.startTime) {
              const hour = new Date(segment.startTime).getHours();
              return hour >= 6 && hour < 8; // 6-8
            }
            return false;
          });
        }
        return false;
      });
    },
    points: 25
  },
    {
    id: 'weekend_hero',
    name: '',
    description: '5',
    icon: '',
    condition: (userData) => {
      const weekendTasks = Object.entries(userData.tasksByDate)
        .filter(([date]) => {
          const day = new Date(date).getDay();
          return day === 0 || day === 6; // 
        })
        .flatMap(([_, tasks]) => tasks.filter(task => task.done));
      
      return weekendTasks.length >= 5;
    },
    points: 35
  }
  ]
};
