//  App  generateDailyLog 
const generateDailyLog = () => {
  const completedTasks = todayTasks.filter(task => task.done);

  if (completedTasks.length === 0) {
    alert('');
    return;
  }

  const tasksByCategory = {};
  todayTasks.forEach(task => {
    if (!tasksByCategory[task.category]) {
      tasksByCategory[task.category] = [];
    }
    tasksByCategory[task.category].push(task);
  });

  const totalTime = completedTasks.reduce((sum, task) => sum + (task.timeSpent || 0), 0);
  const totalMinutes = Math.floor(totalTime / 60);

  // 
  let logContent = ` ${selectedDate} \n`;

  // Markdown 
  let markdownContent = `# ${selectedDate} \n`;

  // 
  if (dailyRating > 0) {
    const stars = ''.repeat(dailyRating);
    logContent += ` : ${stars}\n`;
    markdownContent += `## : ${stars} \n\n`;
  }
  
  if (dailyReflection) {
    logContent += ` : ${dailyReflection}\n`;
    markdownContent += `## \n${dailyReflection}\n\n`;
  }
  
  logContent += '\n';
  markdownContent += `## \n\n`;

  Object.entries(tasksByCategory).forEach(([category, tasks]) => {
    logContent += ` ${category}:\n`;
    markdownContent += `### ${category}\n`;
    
    tasks.forEach((task, index) => {
      const minutes = task.timeSpent ? Math.floor(task.timeSpent / 60) : 0;
      const timeText = `${minutes}m`; // Xm
      const status = task.done ? '' : '';
      const markdownStatus = task.done ? '- [x]' : '- [ ]';
      
      logContent += `  ${index + 1}. ${status} ${task.text} ${timeText}\n`;
      markdownContent += `${markdownStatus} ${task.text} ${timeText}\n`;
      
      if (task.note) {
        logContent += `     : ${task.note}\n`;
        markdownContent += `  - : ${task.note}\n`;
      }
    });
    logContent += '\n';
    markdownContent += '\n';
  });

  logContent += ` :\n`;
  logContent += `   : ${completedTasks.length} \n`;
  logContent += `   : ${todayTasks.length} \n`;
  logContent += `   : ${Math.round((completedTasks.length / todayTasks.length) * 100)}%\n`;
  logContent += `   : ${totalMinutes} \n`;
  logContent += `   : ${completedTasks.length > 0 ? Math.round(totalMinutes / completedTasks.length) : 0} \n`;

  markdownContent += `## \n`;
  markdownContent += `- : ${completedTasks.length} \n`;
  markdownContent += `- : ${todayTasks.length} \n`;
  markdownContent += `- : ${Math.round((completedTasks.length / todayTasks.length) * 100)}%\n`;
  markdownContent += `- : ${totalMinutes} \n`;
  markdownContent += `- : ${completedTasks.length > 0 ? Math.round(totalMinutes / completedTasks.length) : 0} \n`;

  setShowDailyLogModal({
    visible: true,
    content: logContent,
    markdownContent: markdownContent,
    date: selectedDate,
    stats: {
      completedTasks: completedTasks.length,
      totalTasks: todayTasks.length,
      completionRate: Math.round((completedTasks.length / todayTasks.length) * 100),
      totalMinutes: totalMinutes,
      averagePerTask: completedTasks.length > 0 ? Math.round(totalMinutes / completedTasks.length) : 0,
      categories: Object.keys(tasksByCategory).length,
      dailyRating: dailyRating,
      dailyReflection: dailyReflection
    }
  });
};


